import React from "react"
import Link from "gatsby-link"
import styled from "styled-components"
import Helmet from "react-helmet"
import Fade from "react-reveal/Fade"
import {
  TINY_SHADOW,
  TINY_BKG,
  SECONDARY_TEXT
} from "../../components/consts/colors.js"
import Icon from "../../components/Icons"
import "../../css/index.css"
import "../../css/typography.css"
import "../../css/Hide.css"
import SectionContainer from "../../components/SectionContainer"
import SectionContainerBackground from "../../components/SectionContainerBackground"
import SectionHeaderContent from "../../components/SectionHeaderContent"
import SectionContent from "../../components/SectionContent"
import {
  TinyButton,
  TinyButtonAlt,
  TinyButtonWhite
} from "../../components/Button.js"
import SectionImages from "../../components/SectionImages"
import SectionImage from "../../components/SectionImage"
import FAQHeader from "../../components/FAQHeader"
import payments from "../../assets/payments.png"

import withScreensize from "../../components/withScreensize"
import ScreensizeProvider from "../../components/ScreensizeProvider"

const pageData = {
  FAQHeadline: "Get paid securely with Stripe Connect",
  FAQCopy: (
    <p>
      We use Stripe to handle transactions and Stripe Connect to send you your
      profits. All you'll need to do is follow the{" "}
      <a href="http://my.tinymerch.com/payment">payments setup process</a>,
      which you can do directly from your TinyMerch Dashboard.
      <br />
      <br />
      Once you've connected a card or bank account with Stripe Connect, you'll
      receive your profits on the first of each month. Each bank transfer incurs
      a 25-cent transfer fee.
    </p>
  ),
  FAQLink: "#",
  FAQwhatIsCTA: "Get Started!"
}

const NextQ = styled.div`
  margin: 10px 0px;
  a {
    display: flex;
    transition: all 200ms ease;
    :hover {
      transform: translateX(3px);
    }
    div {
      padding-left: 10px;
      position: relative;
      top: 2px;
    }
  }
`

const GetPaid = withScreensize(props => {
  const isMobile = props.screensize === "phones"
  const isTablet = props.screensize === "thatUglyInBetweenSize"
  const isSmall = props.screensize === "devWithConsoleOpen"
  const isWidescreen = props.screensize === "widescreenFools"

  return (
    <div>
      <Helmet title="TinyMerch | FAQ – What is TinyMerch" />
      <FAQHeader />
      <SectionContainer positionContent="center">
        <SectionContent
          justify="center"
          color
          height={
            isMobile
              ? "300px"
              : isTablet ? "400px" : isWidescreen ? "500px" : "500px"
          }
          width={
            isMobile ? "95%" : isTablet ? "80%" : isWidescreen ? "40%" : "50%"
          }>
          <Fade left delay={50} duration={400}>
            <h2>{pageData.FAQHeadline}</h2>
          </Fade>
          <Fade left delay={100} duration={400}>
            <p style={{ paddingTop: "25px" }}>{pageData.FAQCopy}</p>
            <p>
              We will never charge the card you add! This is purely for you to
              recieve your profits.
            </p>
            <em style={{ color: "rgba(90,88,94,.6)", paddingTop: "25px" }}>
              Want to skip Payments all together? Easily select a charity, and
              we'll donate 100% of your profits directily to them.
            </em>
          </Fade>
          <Fade left delay={150} duration={400}>
            <NextQ>
              <Link to="/faq/how-it-works">
                🤔 So How does it work?
                <Icon name="forward" color="#6079F2" />
              </Link>
            </NextQ>
          </Fade>
          {/* <a href={pageData.FAQLink}>
            <TinyButton>{pageData.FAQwhatIsCTA}</TinyButton>
          </a> */}
        </SectionContent>
        <SectionImages
          width={
            isMobile ? "100%" : isTablet ? "100%" : isWidescreen ? "60%" : "50%"
          }
          height={
            isMobile
              ? "200px"
              : isTablet ? "300px" : isWidescreen ? "600px" : "600px"
          }>
          <Fade right delay={350} duration={500}>
            <SectionImage
              shadow
              src={payments}
              ImgWidth={
                isMobile
                  ? "300px"
                  : isTablet ? "500px" : isWidescreen ? "700px" : "600px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile ? "0%" : isTablet ? "0%" : isWidescreen ? "20%" : "10%"
              }
            />
          </Fade>
        </SectionImages>
      </SectionContainer>
    </div>
  )
})

export default props => (
  <ScreensizeProvider>
    <GetPaid {...props} />
  </ScreensizeProvider>
)



